<template>
	<div class="crui">
		<div class="baib">
			<load v-if="istrue"></load>
			<bscroll class="scc" v-else>
				<ul class="liul">
					<li v-for="item in movielist" :key="item.id" class="bigMovie">
						<div class="leftImg" @tap="hanh(item.id)"><img :src="item.images.small" style="width: 100px;height:128px;" /></div>
						<div class="introduce">
							<h2 style="font-size: 16px;" @tap="hanh(item.id)">{{item.title | len}}</h2>
							<p>主演：<b v-for="name in item.casts">  {{name.name}}</b></p>
							<p>类型：{{item.genres | jion}}</p>
							<p>上映时间：{{item.mainland_pubdate}}</p>
						</div>
						<div class="buy_ticket"><p class="buy_ticketP" @tap="showBuy">预售</p></div>
					</li>
				</ul>
			</bscroll>
		</div>
		<div v-show="onno" class="tank">暂时没有此功能</div>
	</div>
</template>
<script>
	export default{
		name:"zhudi",
		data(){
			return{
				movielist:[],
				istrue:true,
				onno:false,
				prevCityId:-1
			}
		},
		methods:{
			showBuy(){
				this.onno = true;
				setTimeout(()=>{this.onno = false;},800)
			},
			hanh(relId){
				this.$router.push("/sousuo/detail/"+relId);
			}
		},
		activated(){
			const cityId = this.$store.state.city.id;
			if (this.prevCityId == cityId) {return}
				this.istrue = true;
			this.axios.get('https://douban.uieee.com/v2/movie/coming_soon').then(res =>{
				const msg = res.statusText;
				if(msg == 'OK'){
					this.movielist = res.data.subjects;
					this.istrue = false;
				}
			})
		/*	this.axios.get("/api/movieComingList?cityId="+cityId).then((res) => {
				const msg = res.data.msg;
				if(msg == 'ok'){
					this.prevCityId = cityId;
					this.movielist = res.data.data.comingList;
					this.istrue = false;	
				}
			})*/
		}
	}
</script>
<style scoped>
	*{
		margin:0;
		padding:0;
	}
	.crui{
		width: 100%;
		height: calc(100vh - 100px);
		overflow: hidden;
	}
	.baib{
		width: 100%;
		height: 100%;
	}
	.liul{
		margin-right: 5px;
		margin-left: 5px;
		padding-bottom: 50px;
	}
	.bigMovie{
		width: 100%;
		display: flex;
		border-bottom: 1px solid #ddd;
		padding-bottom: 5px;
	}
	.introduce h2{
		padding-top: 10px;
	}
	.leftImg,.buy_ticket{
		flex: 1;
	}
	.tank{
		position: absolute;
		top: 40%;
		left: 50%;
		width: 150px;
		line-height: 35px;
		transform: translate(-50%,-50%);
		text-align: center;
		border-radius: 5px;
		color: white;
		font-size: 18px;
		font-weight: bold;
		background-color: skyblue;
	}
	.buy_ticketP{
		position: relative;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		text-align: center;
		background: #66b3ff;
		text-align: center;
		border-radius: 3px;
		width: 50px;
		height: 25px;
		line-height: 25px;
	}
	.introduce{
		flex: 3;
		margin-left:5px;
		margin-right: 5px; 
	}
	.introduce p{
		color: #aaa;
		font-size: 13px;
		padding-top:5px;
	}
</style>


